export default {
    methods: {
        // 
        changeStyle() {},

        // 
        drawArrow(x1, y1, x2, y2) {
            // (x1, y1)  (x2, y2)
            // 
            // let endRadians = (90 * Math.PI) / 180;
            // 

            // 
            // this.cxt.save();
            // this.cxt.beginPath();
            // this.cxt.fillStyle = '#d8d8d8';
            // //  => (x2, y2)
            // this.cxt.translate(x2, y2);
            // this.cxt.rotate(endRadians);
            // this.cxt.moveTo(0, 0);
            // this.cxt.lineTo(5, 10);
            // this.cxt.lineTo(-5, 10);
            // this.cxt.closePath();
            // this.cxt.fill();
            // this.cxt.restore();
            // 

            // 
            this.cxt.save();
            this.cxt.lineWidth = 4;
            this.cxt.strokeStyle = '#4EBABA';
            this.cxt.beginPath();
            let cp = [x1 + (x1 - x2) * Math.sign(x1 - x2) * 0.4, y1];
            let cp2 = [x2 - (x2 - x1) * Math.sign(x2 - x1) * 0.4, y2];
            this.cxt.moveTo(x1, y1);
            this.cxt.bezierCurveTo(cp[0], cp[1], cp2[0], cp2[1], x2, y2);
            this.cxt.lineCap = "round";
            this.cxt.stroke();
            this.cxt.restore();
            // 
        }
    }
};