      format("embedded-opentype"),
    url("//at.alicdn.com/t/font_1910387_e0ec74axkzm.woff2") format("woff2"),
    url("//at.alicdn.com/t/font_1910387_e0ec74axkzm.woff") format("woff"),
    url("//at.alicdn.com/t/font_1910387_e0ec74axkzm.ttf") format("truetype"),
