<!--
* @componmentName node
* @module "src\views\analysisTool\myResultPage\components\toolMain\workflow\node.vue"
* @fileDescription 
* @author li
* @date 2020825
* @param {Object} [nodesData] 
* @param {Number} [radius] 
* @param {Event} [handleNodeClick] 
* @example 
* <node-canvas :nodesData="nodesData" :radius="radius" @handleNodeClick="clickNode"></node-canvas>
 -->

<template>
  <div class="nodeCanvas" id="consize" ref="consize" v-resize="resize">
    <div class="title">{{ nodesData.name }}</div>
    <canvas
      class="canvas"
      :height="canvasHeight"
      :width="canvasWidth"
      :class="{ active: modifyClass }"
      id="test"
      @click="triggerCanvasClick($event)"
      @mousemove="readMousePath($event)"
    ></canvas>
  </div>
</template>

<script>
import resize from "../../../../../plugins/directivesSet/resize";

export default {
  name: "nodeCanvas",

  props: {
    // 
    nodesData: [Object, Array],
    // 
    radius: Number,
  },

  data() {
    return {
      // 
      elements: [],
      // 
      links: [],
      // canvas
      canvasSize: {},
      // 
      modifyClass: false,
      // 
      canvasHeight: 0,
      canvasWidth: 0,
    };
  },

  // 
  directives: {
    resize,
  },

  created() {},

  mounted() {
    this.canvasHeight =
      this.$refs.consize.offsetHeight * window.devicePixelRatio;
    this.canvasWidth = this.$refs.consize.offsetWidth * window.devicePixelRatio;
    // 
    this.canvas = document.getElementById("test");
    // canvas
    if (this.canvas.getContext) {
      //2d
      this.ctx = this.canvas.getContext("2d");
      // 
      this.launchCanvas();
    }
  },

  methods: {
    draw(radius, w, h, name, color) {
      this.ctx.beginPath(); // 
      this.ctx.fillStyle = color || "rgb(255,255,255)"; // 
      this.ctx.shadowColor = "#d8d8d8"; // 
      this.ctx.shadowBlur = 20; // 
      //ctx.arc(x, y, radius, startAngle, endAngle, anticlockwise);anticlockwise Boolean  true
      this.ctx.arc(w, h, radius, 0, Math.PI * 2, false); // 
      this.ctx.closePath(); // 
      this.ctx.fill(); // 

      // sizefamily
      this.ctx.font = "14px Trebuchet MS";

      this.ctx.textAlign = "center";
      // 
      this.ctx.strokeStyle = color;
      // 
      this.ctx.strokeText(name, w, h + 5);
      // 
      this.ctx.fillStyle = "#fff";
      // 
      this.ctx.fillText(name, w, h + 5);
    },

    // 
    drawArrow(x1, y1, x2, y2) {
      // (x1, y1)  (x2, y2)
      // 
      let endRadians = (90 * Math.PI) / 180;
      // 

      // 
      this.ctx.save();
      this.ctx.beginPath();
      this.ctx.fillStyle = "#d8d8d8";
      //  => (x2, y2)
      this.ctx.translate(x2, y2);
      this.ctx.rotate(endRadians);
      this.ctx.moveTo(0, 0);
      this.ctx.lineTo(5, 10);
      this.ctx.lineTo(-5, 10);
      this.ctx.closePath();
      this.ctx.fill();
      this.ctx.restore();
      // 

      // 
      this.ctx.save();
      this.ctx.lineWidth = 1;
      this.ctx.strokeStyle = "#d8d8d8";
      this.ctx.beginPath();
      let cp = [x1 + (x1 - x2) * Math.sign(x1 - x2) * 0.4, y1];
      let cp2 = [x2 - (x2 - x1) * Math.sign(x2 - x1) * 0.4, y2];
      this.ctx.moveTo(x1, y1);
      this.ctx.bezierCurveTo(cp[0], cp[1], cp2[0], cp2[1], x2, y2);
      this.ctx.stroke();
      this.ctx.restore();
      // 
    },

    // 
    triggerCanvasClick(event) {
      // canvas
      let x = event.offsetX;
      let y = event.offsetY;
      this.elements.forEach((res) => {
        if (
          y > res.y - this.radius &&
          y < res.y + this.radius &&
          x > res.x - this.radius &&
          x < res.x + this.radius
        ) {
          // 
          this.$emit("handleNodeClick", res);
        }
      });
    },

    // 
    readMousePath(event) {
      // 
      let readMousePosition = () => {
        let x = event.offsetX;
        let y = event.offsetY;
        this.modifyClass = this.elements.some((ele) => {
          return (
            y > ele.y - this.radius &&
            y < ele.y + this.radius &&
            x > ele.x - this.radius &&
            x < ele.x + this.radius
          );
        });
      };
      setInterval(readMousePosition(), 1000);
    },

    // 
    launchCanvas() {
      // 
      this.canvasSize = {
        // 
        width: this.$refs.consize.offsetWidth,
        // 
        height: this.$refs.consize.offsetHeight,
      };

      // console.log(this.canvas.width);
      // this.

      // this.canvas.height =
      //   this.$refs.consize.offsetHeight * window.devicePixelRatio;
      // this.canvas.width =
      //   this.$refs.consize.offsetWidth * window.devicePixelRatio;

      //  Math.maxapply() this
      let maxCount = Math.max.apply(
        null,
        this.nodesData.nodes.map((a) => a.count)
      );
      let maxHeight = this.nodesData.maxHeight;
      let consizeBox = document.getElementById("consize");
      if (maxCount * (this.radius + 5) * 2 > this.canvasSize.height) {
        window.console.log("");
        consizeBox.setAttribute("height", maxCount * (this.radius + 5) * 2);
        this.canvasSize.height = this.$refs.consize.offsetHeight;
      }
      if (maxHeight * (this.radius + 5) * 2 + 200 > this.canvasSize.width) {
        window.console.log("");
        consizeBox.setAttribute(
          "width",
          maxHeight * (this.radius + 5) * 2 + 200
        );
        this.canvasSize.width = this.$refs.consize.offsetWidth;
      }

      this.canvas.setAttribute(
        "width",
        this.canvasSize.width * window.devicePixelRatio
      );
      this.canvas.setAttribute(
        "height",
        this.canvasSize.height * window.devicePixelRatio
      );

      this.ctx.scale(window.devicePixelRatio, window.devicePixelRatio);
      // 
      //
      this.elements = [];
      this.nodesData.nodes.forEach((ele) => {
        // 
        let w =
          ((this.canvasSize.width -
            this.radius * (this.nodesData.maxHeight + 1)) /
            this.nodesData.maxHeight) *
          ele.height;
        if (this.canvasSize.width - 160 < w) {
          w = this.canvasSize.width - 160;
        }
        // 
        let h = (this.canvasSize.height / (ele.count + 1)) * ele.num;
        switch (ele.state) {
          case "success":
            ele.state = "#65b858";
            break;
          case "running":
            ele.state = "#f5ca05";
            break;
          case "failed":
            ele.state = "#e54545";
            break;
          case "upstream_failed":
            ele.state = "#808080";
            break;
          case "queued":
            ele.state = "#5F7EC6";
            break;
          case "schedule":
            ele.state = "#4EBABA";
            break;
        }
        this.elements.push({
          id: ele.id,
          // 
          allname: ele.allName,
          name: ele.name,
          // 
          x: w + 80,
          // 
          y: h,
          params: ele.params,
          state: ele.state,
        });
      });

      // node
      //
      this.links = [];
      //sourceheight
      this.nodesData.edges.forEach((inx) => {
        let _a = inx.output.replace(/[^0-9]/gi, "");
        let filterArr_1 = this.nodesData.nodes.filter(function (elem) {
          if (elem.id == _a) {
            // name
            return elem.name;
          }
        });
        let _b = inx.input.replace(/[^0-9]/gi, "");
        let filterArr_2 = this.nodesData.nodes.filter(function (inde) {
          if (inde.id == _b) {
            // name
            return inde.name;
          }
        });
        // 

        // console.log(filterArr_1,filterArr_2)
        let obj = {
          source: filterArr_1[0].id,
          target: filterArr_2[0].id,
        };
        // pushdata
        this.links.push(obj);
      });

      // canvas
      this.links.forEach((ele) => {
        // 
        let x1 = this.elements.filter(function (res) {
          if (res.id == ele.source) {
            return [res.x, res.y];
          }
        });
        // 
        let x2 = this.elements.filter(function (res) {
          if (res.id == ele.target) {
            return [res.x, res.y];
          }
        });
        //  
        this.drawArrow(
          x1[0].x + this.radius + 2,
          x1[0].y,
          x2[0].x - this.radius - 2,
          x2[0].y
        );
      });

      // 
      this.elements.forEach((val) => {
        this.draw(this.radius, val.x, val.y, val.allname, val.state);
      });
    },

    // 
    resize() {
      // 
      this.launchCanvas();
    },
  },
  watch: {
    nodesData() {
      this.launchCanvas();
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.nodeCanvas {
  width: 100%;
  min-height: 22.9375rem;
  background: #fff;
  border-radius: 1.5625rem;
  position: relative;
  .title {
    margin-bottom: 1.875rem;
    font-size: 2rem;
    font-weight: 650;
    opacity: 0.4;
    position: absolute;
    margin: 1.875rem;
    font-style: italic;
    color: #4061ad;
  }
  canvas {
    position: absolute;
    width: 100%;
    min-height: 22.9375rem;
  }
  .active {
    cursor: pointer;
  }
}
</style>
