export default {
    methods: {
        /** 
         *@param cxt:canvas 
         *@param x:x 
         *@param y:y 
         *@param width: 
         *@param height: 
         *@param radius: 
         *@param fillColor: 
         **/
        fillRoundRect(cxt, x, y, width, height, radius, /*optional*/ fillColor, shadowColor, titleColor, name, fontSize) {
            //          
            if (2 * radius > width || 2 * radius > height) {
                return false;
            }
            cxt.save();
            cxt.translate(x, y);
            //  
            this.drawRoundRectPath(cxt, width * this.zoom, height * this.zoom, radius);

            cxt.fillStyle = fillColor || "#fff"; //  
            cxt.shadowColor = shadowColor || "#cecece"; // 
            cxt.shadowBlur = 15 * this.zoom; // 

            // 
            // 
            cxt.fill();
            // 
            cxt.clip();
            // 
            cxt.fillStyle = titleColor || "#4EBABA";
            // 
            cxt.fillRect(0, 0, width * this.zoom, 35 * this.zoom);

            // 
            cxt.fillStyle = titleColor || "#cecece";
            cxt.fillRect(10 * this.zoom, 70 * this.zoom, (width - 20) * this.zoom, 1 * this.zoom);

            cxt.restore();

            cxt.font = "bold " + fontSize * this.zoom + "px Trebuchet MS";
            cxt.textAlign = "start";
            cxt.textBaseline = "top";
            let txt = name;
            cxt.fillStyle = "#fff";
            cxt.fillText(txt, x + 35 * this.zoom, y + 12 * this.zoom);

            cxt.fillStyle = '#fff'; //1
            cxt.font = fontSize * this.zoom + 'px IconFont'; //  !!! IconFont
            let icon = this.iconFont("icon_0");
            cxt.fillText(icon, x + 10 * this.zoom, y + 10 * this.zoom);
            cxt.font = (fontSize * this.zoom - 2) + "px " + "Trebuchet MS";

            this.drawConnectionPoint(cxt, x, y, width, fillColor, name, fontSize);
        },

        // 
        drawConnectionPoint(cxt, x, y, width, fillColor, name, fontSize) {
            cxt.fillStyle = "#cecece";
            cxt.font = fontSize * this.zoom + 'px IconFont'; //  !!! IconFont
            let icon_2 = this.iconFont("icon_2");
            cxt.fillText(icon_2, x + 10 * this.zoom, y + 45 * this.zoom);
            cxt.font = (fontSize * this.zoom - 2) + "px " + "Trebuchet MS";

            cxt.fillStyle = "#cecece";
            cxt.font = fontSize * this.zoom + 'px IconFont'; //  !!! IconFont
            // let icon_2 = this.iconFont("icon_2");
            cxt.fillText(icon_2, x + (width - 26) * this.zoom, y + 80 * this.zoom);
            cxt.font = (fontSize * this.zoom - 2) + "px " + "Trebuchet MS";

            cxt.fillStyle = "#808080";
            cxt.fillText("Input point", x + 40 * this.zoom, y + 47 * this.zoom);
            let textWidth = cxt.measureText("Input point").width;
            cxt.fillText("Output point", x + (width - 46) * this.zoom - textWidth, y + 82 * this.zoom);
        },

        drawRoundRectPath(cxt, width, height, radius) {
            cxt.beginPath(0);
            //01/2PI  
            cxt.arc(width - radius, height - radius, radius, 0, Math.PI / 2);

            //  
            cxt.lineTo(radius, height);

            //1/2PIPI  
            cxt.arc(radius, height - radius, radius, Math.PI / 2, Math.PI);

            //  
            cxt.lineTo(0, radius);

            //PI3/2PI  
            cxt.arc(radius, radius, radius, Math.PI, Math.PI * 3 / 2);

            //  
            cxt.lineTo(width - radius, 0);

            //  
            cxt.arc(width - radius, radius, radius, Math.PI * 3 / 2, Math.PI * 2);

            //  
            cxt.lineTo(width, height - radius);
            cxt.closePath();
        }
    }
};